import React, { Component } from 'react';
import ProgressBar from './ProgressBar';

class Languages extends Component {
    state = {
        languages: [
            {id: 1, value: "Javascript", xp:1.6},
            {id: 2, value: "Css", xp:1.8},
            {id: 3, value: "C",xp:1.5},
            {id: 4, value: "Shell/Bash",xp:1.3},
            {id: 5, value: "Html",xp:2}
        ],
        frameworks: [
            {id: 1, value: "React", xp:1.4},
            {id: 2, value: "Bootstrap", xp:2},
            {id: 3, value: "Sass",xp:1.5},
            {id: 4, value: "Express",xp:0.5}    
        ],
    }

    render() {
        let {languages, frameworks} = this.state;
        return (
            <div className="languagesFrameworks">
                <ProgressBar
                    languages={languages}
                    className="languagesDisplay"
                    title="langages"
                />
                <ProgressBar
                    languages={frameworks}
                    title="frameworks et bibliotheques"
                    className="frameworksDisplay"
                />
                
            </div>
        );
    }
}

export default Languages;